header {
    font-family: 'Montserrat';
}

nav {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    background-color: rgba(231, 231, 231, 0.953);
    width: 100%;
    margin: 0px;
    padding: 0px;
}

.nav-logo h1 {
    font-weight: 800;
    font-size: 1.5rem;
}

.nav-item {
    list-style-type: none;
    text-align: center;
    cursor: pointer;
}

.nav-item li {
    text-decoration: none;
    display: inline;
    padding: 0px 18px;
    font-size: 1.rem;
    font-weight: 600;
    transition: all 0.3s ease;
    -webkit-transition: all 0.3s ease;
    -moz-transition: all 0.3s ease;
    -ms-transition: all 0.3s ease;
    -o-transition: all 0.3s ease;
}

.nav-item li:hover {
    color: rgb(157, 129, 229);
}